import {
	RouterMount,
	createRouter
} from 'uni-simple-router';

const router = createRouter({
	platform: process.env.VUE_APP_PLATFORM,
	routes: [...ROUTES]
});
//
router.beforeEach((to, from, next) => {
	//
	if (to.meta.auth) {
		next();
	} else {
		next();
	}
});
//  
router.afterEach((to, from) => {})

export {
	router,
	RouterMount
}