//  
import config from '@/config.js'
// http
import request from '@/utils/request.js'
import {
	login
} from '../api/user'
// 
// import {
// 	authentication
// } from '@/api/user.js'
import {
	userInfo
} from "@/api/public.js"
export default {
	/**
	 * @
	 */
	routerTo(url, type = 'navigateTo') {
		uni[type]({
			url
		})
	},
	routerGo(url, type = 'navigateTo') {
		let token = uni.getStorageSync('userinfo').token
		if (token) {
			uni[type]({
				url
			})
		} else {
			uni.showToast({
				'title': '',
				icon: 'none'
			})
		}
	},
	/**
	 * @
	 */
	routerBack(delta = 1) {
		uni.navigateBack({
			delta
		})
	},

	/**
	 * @
	 */
	mkm(x) {
		let nub = Math.round((x / 100) / 10);
		return nub.toFixed(2)
	},
	/**
	 * @
	 */
	BumberPrecision(x) {
		return Number(x).toFixed(2)
	},
	/**
	 * @
	 */
	getWxUserInfo() {
		return new Promise((reslove, reject) => {
			uni.getUserInfo({
				success(data) {
					reslove(data)
				},
				fail(err) {
					reject(err)
				}
			})


		})
	},
	setuser() {
		userInfo().then(user => {
			if (user.code == 1) {
				uni.setStorageSync('user', user.data)
			}
		})


	},
	/**
	 * @
	 * @orderInfo 
	 * @url
	 */
	wxparApi(orderInfo, url, map, rest, navidx) {
		let that = this
		wx.requestPayment({
			timeStamp: orderInfo.timeStamp,
			nonceStr: orderInfo.nonceStr,
			package: orderInfo.package,
			signType: orderInfo.signType,
			paySign: orderInfo.paySign,
			success(res) {
				if (url) {
					that.subscription(map, url, navidx, rest)
				} else {
					uni.showToast({
						title: '',
						icon: 'none',
						duration: 2000,
						success() {
							if (navidx == 1) {
								uni.navigateBack()
							}
							if (navidx == 2) {
								that.unloadpage()
								return
							}
							uni.reLaunch({
								url: url
							})
							that.setuser()
						}
					})
				}
			},
			fail(res) {
				uni.showToast({
					title: '',
					icon: 'error',
					duration: 2000,
					success() {
						if (navidx == 1) {

						} else if (navidx == 2) {
							that.unloadpage()
						} else {

							uni.reLaunch({
								url: url
							})
						}
						that.setuser()
					}
				})
			}
		})
	},

	/**
	 * @code
	 */
	loginApi() {
		return new Promise((reslove, reject) => {
			uni.login({
				success(res) {
					reslove(res)
				},
				fail(err) {
					reject(err)
				}
			})

		})
	},
	/**
	 * @
	 */
	chooseImage(count = 1) {
		return new Promise((reslove, reject) => {
			uni.chooseImage({
				count,
				success(data) {
					reslove(data)
				},
				fail(err) {
					reject(err)
				}
			})

		})
	},
	/**
	 * @
	 */
	uploadImg(url) {
		let that = this
		return new Promise(async (relove, reject) => {
			let tempFilePaths // 
			// 
			if (!url) {
				await that.chooseImage().then(d => {
					tempFilePaths = d.tempFilePaths[0]
				}).catch(err => {
					tempFilePaths = err
				})
			} else {
				tempFilePaths = url
			}

			let {
				token
			} = uni.getStorageSync('userinfo')
			// 
			uni.uploadFile({
				url: config.serverHost + '/api/common/upload',
				fileType: 'image',
				filePath: tempFilePaths,
				name: 'file',
				header: {
					token
				},
				success: res => {
					let result = JSON.parse(res.data)
					relove(result.data.fullurl)
				},
				fail(err) {
					reject(err)
				}
			})

		})
	},

	/**
	 * @
	 */
	uploadimage(url) {
		let that = this
		return new Promise(async (relove, reject) => {
			let tempFilePaths // 
			// 
			if (!url) {
				await that.chooseImage().then(d => {
					tempFilePaths = d.tempFilePaths[0]
				}).catch(err => {
					tempFilePaths = err
				})
			} else {
				tempFilePaths = url
			}
			// 
			uni.uploadFile({
				url: config.serverHost + '/addons/fzdc/common/upload',
				fileType: 'image',
				filePath: tempFilePaths,
				name: 'file',

				success: res => {
					let result = JSON.parse(res.data)
					relove(result.data)
				},
				fail(err) {
					reject(err)
				}
			})

		})
	},
	/**
	 * @
	 */
	scanCode() {
		return new Promise((reslove, reject) => {
			uni.scanCode({
				success: (data) => {
					reslove(data)
					// this.rou
				},
				fail(err) {
					reject(err)
				}
			})
		})
	},
	/**
	 * @
	 */
	openTel(phoneNumber) {
		uni.makePhoneCall({
			phoneNumber
		});
	},
	routerGo(url, type = 'navigateTo') {
		let token = uni.getStorageSync('userinfo').token
		if (token) {
			uni[type]({
				url
			})
		} else {
			uni.showToast({
				'title': '',
				icon: 'none'
			})
		}
	},
	showToast(res) {
		uni.showToast({
			title: res,
			icon: 'none'
		})
	},
	/**
	 * @
	 * @String 
	 */
	setClipboardData(data) {
		uni.setClipboardData({
			data: data,
			showToast: true,
			success: function() {}
		});
	},
	/**
	 * @get
	 */
	getParams: obj => {
		let str = '?'
		for (let s in obj) {
			str += `${s}=${obj[s]}&`
		}
		return str.substr(0, str.length - 1)
	},
	/**
	 * @
	 */
	viewImage(e, urls) {
		urls = urls.map(obj => {
			return obj = uni.getStorageSync('loginInfo').imgUrl + '/' + obj
		})
		uni.previewImage({
			urls,
			current: e.currentTarget.dataset.url
		});
	},
	/**
	 * @
	 */
	getLoginInfo() {
		let that = this
		return new Promise(function(relove, reject) {
			that.getLoginCode().then(code => {
				authentication({
					code
				}).then(d => {
					uni.setStorageSync('loginInfo', d.data)
					relove(d)
				}).catch(reject)
			})
		})
	},
	/**
	 * @
	 */
	getDistance(lat1, lng1, lat2, lng2) {
		var radLat1 = lat1 * Math.PI / 180.0;
		var radLat2 = lat2 * Math.PI / 180.0;
		var a = radLat1 - radLat2;
		var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0;
		var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
			Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
		s = s * 6378.137; // EARTH_RADIUS;
		s = Math.round(s * 10000) / 10;
		return s; // m
	},
	/**
	 * @
	 */
	getLocation() {
		return new Promise((relove, reject) => {
			// 
			uni.getLocation({
				success: data => {
					relove(data)
				},
				fail(err) {
					wx.getSetting({
						success: (res) => {
							var statu = res.authSetting;
							if (!statu['scope.userLocation']) {
								wx.showModal({
									title: '',
									content: '',
									success(tip) {
										if (tip.confirm) {
											wx.openSetting({
												success: data => {
													if (data
														.authSetting[
															"scope.userLocation"
														] === true
													) {
														wx.showToast({
															title: '',
															icon: 'success',
															duration: 1000
														})
														//chooseLocation
														setTimeout(
															function() {
																uni.getLocation({
																	success: data => {
																		relove
																			(
																				data
																			)
																	},
																	fail(
																		err
																	) {
																		reject
																			(
																				err
																			)
																	}
																})

															}, 1000)
													} else {
														reject()
													}
												},
												fail() {

												}
											})
										} else {
											wx.showToast({
												title: '',
												icon: 'none',
												duration: 1000
											})
											reject()
										}
									}
								})

							} else {
								uni.getLocation({
									success: data => {
										relove(data)
									},
									fail(err) {
										reject(err)
									}
								})
							}
						}
					})
				}
			})
		})
	},
	/**
	 * @uni
	 */
	uniGetLocation() {
		return new Promise((relove, reject) => {
			uni.getLocation({
				success: d => {
					relove(d)
				},
				fail: d => {
					reject(d)
				}
			})

		})
	},
	/**
	 * @
	 */
	wxgetFuzzyLocation() {
		return new Promise((relove, reject) => {
			wx.getFuzzyLocation({
				type: 'wgs84',
				success: d => {
					relove(d)
				},
				fail: d => {
					uni.showToast({
						title: '',
						icon: 'none'
					})
					reject(d)
				}
			})
		})
	},
	/**
	 * @
	 */
	getchooseLocation() {
		return new Promise((relove, reject) => {
			uni.chooseLocation({
				success: function(d) {
					relove(d)
				},
				fail: function(d) {
					reject(d)
				}
			});
		})
	},
	/**
	 * @
	 */
	getopenLocation(latitude, longitude) {
		return new Promise((relove, reject) => {
			uni.openLocation({
				latitude: Number(latitude),
				longitude: Number(longitude),
				success: function(d) {
					relove(d)
				},
				fail: function(d) {
					reject(d)
				}
			});
		})
	},

	/**
	 * @url
	 */
	get_url(url, name) {
		var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
		var trueUrl1 = url.split('?')[2];
		var trueUrl2 = trueUrl1.match(/(\S*)#/)[1];
		var r = trueUrl2.match(reg);
		if (r != null) {
			return unescape(r[2]);
		}
		return null;
	},
	/**
	 * @
	 */
	getStorage(key) {
		return uni.getStorageSync(key) || ''
	},
	unloadpage() {
		const pages = getCurrentPages()
		// pagesgetCurrentPages
		const curPage = pages[pages.length - 1]
		// 
		curPage.onLoad(curPage.options) // 
		curPage.onShow()
		curPage.onReady()
	},

	subRouter(url, navidx) {
		let that = this
		if (navidx == 1) {
			uni.navigateBack()
		} else if (navidx ==
			2) {
			that.unloadpage()
		} else if (navidx == 3) {
			uni.redirectTo({
				url
			})
		} else {
			uni.reLaunch({
				url
			})
		}
		uni.hideLoading()
	},
	/**
	 * @ tmp,url
	 */
	subscription(tmp, url, navidx, rest) {
		uni.showLoading()
		let that = this
		uni.requestSubscribeMessage({
			tmplIds: tmp,
			success(res) {
				that.subRouter(url, navidx)
			},
			fail(res) {
				that.subRouter(url, navidx)
			},
		})
	},

	/**
	 * @ APP.jsonLaunch
	 */
	onLaunch(options) {
		this.overShare()
	},
	overShare() {
		wx.onAppRoute((res) => {
			let pages = getCurrentPages()
			let view = pages[pages.length - 1]
			if (view) {
				wx.showShareMenu({
					withShareTicket: true,
					menus: ['shareAppMessage', 'shareTimeline'],
					success(res) {},
					fail(e) {}
				})
			}
		})
	}


}