/**
 * function: 606060**124**115**
 * @number   
 */
function formatMsgTime(number) {
  var dateTime = new Date(number); // 
  var Y = dateTime.getFullYear(); // 
  var M = dateTime.getMonth() + 1; // 
  var D = dateTime.getDate(); // 
  var h = dateTime.getHours(); // 
  var m = dateTime.getMinutes(); // 
  var millisecond = dateTime.getTime(); // 
  var now = new Date(); // 
  var nowNew = now.getTime(); // 
  var milliseconds = 0;
  var numberStr;
  milliseconds = nowNew - millisecond;
  if (milliseconds <= 1000 * 60 * 1) { // 
    numberStr = ''
  } else if (1000 * 60 * 1 < milliseconds && milliseconds <= 1000 * 60 * 60) { // 
    numberStr = Math.round((milliseconds / (1000 * 60))) + ''
  } else if (1000 * 60 * 60 * 1 < milliseconds && milliseconds <= 1000 * 60 * 60 * 24) { // 
    numberStr = Math.round(milliseconds / (1000 * 60 * 60)) + ''
  } else if (1000 * 60 * 60 * 24 < milliseconds && milliseconds <= 1000 * 60 * 60 * 24 * 15) { // 
    numberStr = Math.round(milliseconds / (1000 * 60 * 60 * 24)) + ''
  } else if (milliseconds > 1000 * 60 * 60 * 24 * 15 && Y === now.getFullYear()) {
    numberStr = M + '-' + D + ' ' + h + ':' + m
  } else {
    numberStr = Y + '-' + M + '-' + D + ' ' + h + ':' + m
  }
  return numberStr
}
 
/**
 * function: 
 * @number   
 * @type     1-- --2--
 */
function toDate(number, type) {
  var date = new Date(number);
  var Y = date.getFullYear();
  var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
  var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
  var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
  var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
  var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
  if (type == '1') {
    return Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s;
  } else if (type == '2') {
    return Y + '-' + M + '-' + D;
  }
}
 
module.exports = {
  toDate: toDate,
  formatMsgTime: formatMsgTime
}
