import Vue from "vue"
import Vuex from 'vuex'
Vue.use(Vuex)
import createPersistedState from 'vuex-persistedstate'



let lifeData = {}

try {
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData')
} catch (e) {

}

// APPstate 
let saveStatekeys = [
	'vuex_token',
	'islogin',
	'producteDetail'
]
// 
const saveLifeData = (key, value) => {
	// indexOf  -1
	//   
	if (saveStatekeys.indexOf(key) != -1) {
		let tmp = uni.getStorageSync('lifeData')
		// lifeData{}  lifeDate  
		tmp = tmp ? tmp : {}
		// tmp  
		tmp[key] = value
		// 
		uni.setStorageSync('lifeData', tmp);
	}
}

// saveStatekeyssaveStatekeys  lifeData

const store = new Vuex.Store({
	state: {
		vuex_token: lifeData.vuex_token ? lifeData.vuex_token : '',
		vuex_address: null,
		islogin: false,
		producteDetail: ''
	},
	mutations: {
		setproducteDetail(state, val) {
			state.producteDetail = val;
			// uni.setStorageSync('producteDetail', val)
			// console.log(state.producteDetail, val, '')
		},
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.'); //. 
			let saveKey = '';
			let len = nameArr.length; //
			if (len >= 2) { //
				let obj = state[nameArr[0]]; //
				for (let i = 1; i < len - 1; i++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state  
				state[payload.name] = payload.value; //state
				saveKey = payload.name; //saveKey  
			}
			// 
			// saveKey    state[saveKey] value
			saveLifeData(saveKey, state[saveKey])
		},
		setislogin(state, flag) {
			state.islogin = flag;
		},
		// 
		cellphone(state) {
			let barHeight = 0
			let StatusBar = 0
			let barHightTop = ''
			let systemInfo = uni.getSystemInfoSync();
			let custom = uni.getMenuButtonBoundingClientRect();
			var statusBarHeight = systemInfo.statusBarHeight
			//statusBarHeight
			let CustomBar = custom.bottom + custom.top - systemInfo.statusBarHeight;
			barHeight = CustomBar || 0
			StatusBar = statusBarHeight || 0
			//StatusBar + 'px' barHeight - StatusBar+ 'px'  barHeight + StatusBar+ 'px'
			barHightTop = StatusBar * 2

			state.cellphone = barHightTop
		}
	},
	plugins: [
		createPersistedState({
			storage: {
				getItem: key => uni.getStorageSync(key), // 
				setItem: (key, value) => uni.setStorageSync(key, value), // 
				removeItem: key => uni.removeStorageSync(key) // 
			},
			paths: ['producteDetail'], // 
		})
	]
})

export default store;