import
request
from "../utils/request.js"
// 
export function common(data) {
	return request({
		url: 'common/common',
		method: 'POST',
		data
	})
}
// 
export function userInfo(data) {
	return request({
		url: 'user/userInfo',
		method: 'POST',
		data
	})
}
// 
export function user_profile(data) {
	return request({
		url: 'user/profile',
		method: 'POST',
		data
	})
}
// 
export function upload(data) {
	return request({
		url: 'common/upload',
		method: 'POST',
		data
	})
}
// 
export function code(data) {
	return request({
		url: 'user/code',
		method: 'POST',
		data
	})
}