import {
	mapState
} from 'vuex'
import store from '@/store/index.js'


// vuex  
let $uStoreKey = []
try {
	// vuexkey
	$uStoreKey = store.state ? Object.keys(store.state) : []
} catch (e) {

}

// https://blog.csdn.net/qq_59747594/article/details/127700908   module.exports
module.exports = {
	created() {
		// this.$u.vuex vuex  
		this.$u.vuex = (name, value) => {
			this.$store.commit('$uStore', {
				name,
				value
			})
		}
	},
	computed: {
		// vuex  
		...mapState($uStoreKey)
	}
}

